import React from 'react'
import Context from 'contexts/Context';
import SuggestionCard from './SuggestionCard';

export default () => 
  <Context.Consumer>
    {({ suggestions }) => (
      <div className="suggestions">
        {suggestions.map(({image, title, description}) => (
          <SuggestionCard
          key={Math.random()*16}
          image={image}
          title={title}
          description={description}
          />
        ))}
      </div>
    )}
  </Context.Consumer>
